.project3-images-wrapper{
    position: relative;
    grid-column: 2;
    grid-row: 1/ span 8;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow-y: auto;
    padding-top: 800px;
}
.project3-image1{
    background-image: url('../../../../images/ecommerce-site-img1.png');
    background-size: cover;
    background-position:center;
    background-repeat: no-repeat;
    background-color: transparent;
}
.project3-image2{
    background-image: url('../../../../images/ecommerce-site-img2.png');
    background-size: cover;
    background-position:center;
    background-repeat: no-repeat;
    background-color: transparent;
}
.project3-image3{
    background-image: url('../../../../images/ecommerce-site-img3.png');
    background-size: cover;
    background-position:center;
    background-repeat: no-repeat;
    background-color: transparent;
}
.project3-image4{
    background-image: url('../../../../images/ecommerce-site-img4.png');
    background-size: cover;
    background-position:center;
    background-repeat: no-repeat;
    background-color: transparent;
}
@media only screen and (max-width: 428px){
    .project3-images-wrapper{
        position: relative;
        grid-column: 1;
        grid-row: 4 / span 4;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        width: 100vw;
        overflow-x: auto;
        overflow-y: hidden;
        margin-left: -30px;
        padding: 0;
        padding-left: 400px;
        padding-right: 100px;
    }
    .project3-image1,.project3-image2,.project3-image3,.project3-image4{
        margin: 0;
        margin-left: 20px;
        width: 250px;
        height: 350px;
    }
    .project3-image1{
        margin-left: 400px;
        background-position-x: -200px;
    }
}